.ico-big {
    width: 48px;
    height: 48px;
    padding: 10px;
    border: 1px solid #e3e3e3;
    box-sizing: border-box;
    border-radius: 50%;
}

.ico {
    svg {
        display: inline-block;
        margin-right: 4px;
        vertical-align: middle;
    }
}

// soporte para invertir iconos con el componente svg-icon
svg-icon {
    &.c-invert {
        svg {
            filter: invert(1);
        }
    }
}
