
/*+------------------------------------------------------------+
 |       -ANIMATIONS    
 +------------------------------------------------------------+*/

 @keyframes underline {
    0% {
        width: 0;
    }
    100% {
        width: 100%;
    }
}

@keyframes opacity {
    0% {
        opacity: 0;
    }
    100% {
        opacity: 1;
    }
}


@keyframes stretch {
    0% {
        height: 0;
    }
    100% {
        height: 80px;
    }
}

@keyframes fadeInDown {
    from {
        opacity: 0;
        -webkit-transform: translate(-50%, -100%);
        transform: translate(-50%, -100%);
    }

    to {
        opacity: 1;
        -webkit-transform: translate(-50%, 0);
        transform: translate(-50%, 0);
    }
}

