
.row-2 {
  box-sizing: border-box;
  display: flex;
  width: 100%;
  -ms-flex-wrap: wrap;
  flex-wrap: wrap;
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  justify-content: center;


  .col {
    display: flex;
    text-align: center;

    flex-basis: 80%;

    @media (min-width:$break-sm) {
      flex-basis: 25%;
    }

    @media (min-width:$break-md) {
      flex-basis: 25%;
    }



    &.sp{
      flex-basis: 80%;

      @media (min-width:$break-sm) {
        flex-basis: 40%;
      }
  
      @media (min-width:$break-md) {
        flex-basis: 50%;
      }
  


    }



  }
}

