/** DENTRO DE UN CONTENT DE UN RATIO CENTRAMOS EL VIDEO**/
.video-cover {
    position: absolute;
    top: 50%;
    left: 50%;
    width: 100%;
    height: 100%;
    object-fit: cover;
    -webkit-transform: translate(-50%, -50%);
    transform: translate(-50%, -50%);
}
