/*+------------------------------------------------------------+
 |       - FASE0: RESET
 +------------------------------------------------------------+*/
html,
body,
div,
span,
select,
object {
  margin: 0;
  padding: 0;
  border: 0;
  outline: 0;
  font-size: 100%;
  vertical-align: baseline;
  background: transparent;
}
a {
  color: inherit;
  text-decoration: none;
}


