
.swiper {
  width: 100%;
  height: 300px;
padding-bottom: 20px;    }
.swiper-slide {
  text-align: center;
  font-size: 18px;
  background: #fff;
background-color: $bg-light;
  display: flex;
  justify-content: center;
  align-items: center;
}
.swiper-slide img {
  display: block;
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.swiper-horizontal>.swiper-pagination-bullets, .swiper-pagination-bullets.swiper-pagination-horizontal, .swiper-pagination-custom, .swiper-pagination-fraction {
bottom: 0px;
left: 0;
width: 100%;
}
.swiper-pagination-bullet{
  background-color: $bg-light;
  opacity: 1;
}
.swiper-pagination-bullet-active {
  background-color: $primary;
  }
.swiper-button-next,.swiper-button-prev {
background-color: $primary;
width: 40px;
height: 40px;
display: flex;
}
.swiper-button-next:after, .swiper-button-prev:after {
font-family: swiper-icons;
font-size: 16px;
text-transform: none!important;
letter-spacing: 0;
text-transform: none;
font-variant: initial;
line-height: 1;
color: white;
}



@import "~swiper/swiper";
@import "~swiper/components/a11y/a11y.scss";
@import "~swiper/components/controller/controller.scss";
@import "~swiper/components/effect-coverflow/effect-coverflow.scss";
@import "~swiper/components/effect-cube/effect-cube.scss";
@import "~swiper/components/effect-fade/effect-fade.scss";
@import "~swiper/components/effect-flip/effect-flip.scss";
@import "~swiper/components/lazy/lazy.scss";
@import "~swiper/components/navigation/navigation.scss";
@import "~swiper/components/pagination/pagination.scss";

$lg-path-fonts: "~lightgallery/fonts";
$lg-path-images: "~lightgallery/images";
@import "~lightgallery/scss/lightgallery.scss";

.swiper-container {
  width: 600px;
  height: 300px;
}

.eedee-background-div {
  height: 100%;
  /* todo is this needed ? */
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  /* end todo is this needed ? */
  overflow-x: hidden;
  overflow-y: hidden;
  grid-area: 1/1;

  /* todo maybe also grid-area ? */
  .eedee-background-div__overlay {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
  }

  video, img {
    object-fit: cover;
    width: 100%;
    height: 100%;
    display: block;
  }
}

.slide-content {
  display: flex;
  position: relative;
  overflow-x: hidden;
  flex-direction: column;
  grid-area: 1/1;

  // to center content perfectly  ...
  // ... remove top margin from first element ...
  > :first-child {
    margin-top: 0;
  }

  // ... remove bottom margin from last element
  > :last-child {
    margin-bottom: 0;
  }

  > * {
    max-width: 100%;
  }
}

.swiper-slide img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}



